import { Fragment, FunctionComponent } from 'preact';
import { Button, CartItem, Image } from '@adobe-commerce/elsie/components';
import { useForm } from '@/order/hooks/components/useForm';
import FormInputs from '../Form/FormInputs';
import {
  modifyFieldsConfig,
  replicateEntries,
} from '@/order/lib/returnOrdersHelper';
import '@/order/components/ReturnReasonForm/ReturnReasonForm.css';
import { Slot } from '@adobe-commerce/elsie/lib';
import { ReturnReasonFormProps } from '@/order/types';

export const ReturnReasonForm: FunctionComponent<ReturnReasonFormProps> = ({
  placeholderImage,
  slots,
  formsRef,
  selectedProductList,
  loading,
  fieldsConfig,
  translations,
  handleChangeStep,
  onSubmit,
}) => {
  const { formData, errors, formRef, handleChange, handleBlur, handleSubmit } =
    useForm({
      fieldsConfig: replicateEntries(fieldsConfig, selectedProductList?.length),
      onSubmit,
    });

  return (
    <form
      className="order-return-reason-form"
      ref={formRef}
      onSubmit={handleSubmit}
      name={'returnReasonForm'}
    >
      {selectedProductList.map((orderItem, index) => {
        const giftCard = orderItem?.giftCard;
        const product = orderItem?.product;
        const fieldsList = modifyFieldsConfig(fieldsConfig, index);
        const formName = `${orderItem?.id}_${index}`;
        const quantity = orderItem?.currentReturnOrderQuantity! ?? 1;

        const configurations = {
          ...(orderItem?.currentReturnOrderQuantity
            ? { [translations.configurationsListQuantity]: quantity }
            : {}),
          ...(orderItem.configurableOptions || {}),
          ...(orderItem.bundleOptions || {}),
          ...(giftCard?.senderName
            ? { [translations.sender]: giftCard?.senderName }
            : {}),
          ...(giftCard?.senderEmail
            ? { [translations.sender]: giftCard?.senderEmail }
            : {}),
          ...(giftCard?.recipientName
            ? { [translations.recipient]: giftCard?.recipientName }
            : {}),
          ...(giftCard?.recipientEmail
            ? { [translations.recipient]: giftCard?.recipientEmail }
            : {}),
          ...(giftCard?.message
            ? { [translations.message]: giftCard?.message }
            : {}),
          ...(orderItem?.downloadableLinks
            ? {
                [`${orderItem?.downloadableLinks?.count} ${translations.downloadableCount}`]:
                  orderItem?.downloadableLinks?.result,
              }
            : {}),
        };

        const productName = orderItem?.product?.name;

        const defaultImageProps = {
          title: productName,
          src: product?.thumbnail?.url ?? placeholderImage,
          alt: productName,
          width: 90,
          height: 120,
          loading: 'lazy',
        } as const;

        return (
          <Fragment key={orderItem.id}>
            <CartItem
              loading={loading}
              title={<div data-testid="product-name">{productName}</div>}
              sku={<div>{product?.sku}</div>}
              image={
                <Slot
                  name="ReturnReasonFormImage"
                  slot={slots?.ReturnReasonFormImage}
                  context={{
                    data: orderItem,
                    defaultImageProps,
                  }}
                >
                  <Image {...defaultImageProps} />
                </Slot>
              }
              configurations={configurations}
            />
            <form
              name={formName}
              ref={formsRef?.current[index]}
              data-quantity={quantity}
            >
              <FormInputs
                className={'className'}
                loading={loading}
                fields={fieldsList}
                onChange={handleChange}
                onBlur={handleBlur}
                errors={errors}
                values={formData}
              />
            </form>
          </Fragment>
        );
      })}
      <Slot
        data-testid="returnFormActions"
        name="ReturnFormActions"
        slot={slots?.ReturnFormActions}
        context={{
          handleChangeStep,
        }}
      >
        <div className="order-return-reason-form__actions">
          <Button
            variant="secondary"
            type="button"
            onClick={() => {
              handleChangeStep('products');
            }}
          >
            {translations.backStep}
          </Button>
          <Button>{translations.submit}</Button>
        </div>
      </Slot>
    </form>
  );
};
