import {
  getAttributesList,
  requestGuestReturn,
  requestReturn,
} from '@/order/api';
import {
  AttributesFormModel,
  OrderDataModel,
  OrderItemModel,
} from '@/order/data/models';
import { StepsTypes, UseCreateReturn } from '@/order/types';
import { useState, useCallback, useEffect, useRef } from 'preact/hooks';
import { createRef, RefObject } from 'preact';
import {
  processFormElement,
  sortItemsByCondition,
} from '@/order/lib/returnOrdersHelper';
import { events } from '@adobe-commerce/event-bus';
import { getQueryParam } from '@/order/lib/getQueryParam';

export const useCreateReturn = ({
  onSuccess,
  onError,
  handleSetInLineAlert,
  orderData,
}: UseCreateReturn) => {
  const [isGuestCreateReturn, setIsGuestCreateReturn] = useState(false);
  const [order, setOrder] = useState({ id: '', email: '', ...orderData });
  const [steps, setSteps] = useState<StepsTypes>('products');
  const [loading, setLoading] = useState(true);
  const [selectedProductList, setSelectedProductList] = useState<
    OrderItemModel[] | []
  >([]);
  const [attributesList, setAttributesList] = useState<
    AttributesFormModel[] | []
  >([]);
  const [itemsEligibleForReturn, setItemsEligibleForReturn] = useState<
    OrderItemModel[]
  >([]);

  const formsRef = useRef<RefObject<HTMLFormElement>[]>([]);

  if (formsRef.current.length !== selectedProductList.length) {
    formsRef.current = selectedProductList.map(
      (_, i) => formsRef.current[i] || createRef<HTMLFormElement>()
    );
  }

  useEffect(() => {
    const event = events.on(
      'order/data',
      (order: OrderDataModel) => {
        setOrder(order);
        const sortedReturnItems = sortItemsByCondition(order);
        setItemsEligibleForReturn(sortedReturnItems);
        setIsGuestCreateReturn(getQueryParam('orderRef')?.length > 20);
        setLoading(false);
      },
      { eager: true }
    );

    return () => {
      event?.off();
    };
  }, []);

  useEffect(() => {
    getAttributesList('RMA_ITEM').then((response) => {
      if (!response?.length) return;

      setAttributesList(response);
      setLoading(false);
    });
  }, []);

  const handleSelectedProductList = useCallback((orderItem: OrderItemModel) => {
    setSelectedProductList((currentList) => {
      const index = currentList.findIndex(
        (item) => item?.productSku === orderItem?.productSku
      );

      if (index > -1) {
        return currentList.filter(
          (item) => item?.productSku !== orderItem?.productSku
        );
      }

      return [...currentList, orderItem];
    });
  }, []);

  const handleChangeStep = useCallback(
    (value: StepsTypes) => {
      setSteps(value);
      handleSetInLineAlert();
      if (value === 'products') {
        setSelectedProductList([]);
      }
    },
    [handleSetInLineAlert]
  );

  const handleSetQuantity = useCallback(
    (value: number, productSku: string) => {
      const result = selectedProductList.map((el) =>
        el.productSku === productSku
          ? { ...el, currentReturnOrderQuantity: value }
          : el
      );
      setSelectedProductList(result);
    },
    [selectedProductList]
  );

  const onSubmit = useCallback(
    async (
      _: SubmitEvent,
      isValid: boolean
    ): Promise<void | null | undefined> => {
      if (!isValid) return;

      setLoading(true);
      const orderReturnInfo = {
        orderUid: order.id,
        contactEmail: order.email,
      };
      const items = processFormElement(formsRef);

      if (!isGuestCreateReturn) {
        requestReturn({ ...orderReturnInfo, items })
          .then((response) => {
            onSuccess?.(response);
            handleChangeStep('success');
            handleSetInLineAlert();
          })
          .catch((error) => {
            onError?.(error.message);
            handleSetInLineAlert({ type: 'error', heading: error.message });
          });
      } else {
        requestGuestReturn({
          token: getQueryParam('orderRef'),
          contactEmail: orderReturnInfo.contactEmail,
          items,
          commentText: '.',
        })
          .then((response) => {
            onSuccess?.(response);
            handleChangeStep('success');
            handleSetInLineAlert();
          })
          .catch((error) => {
            onError?.(error.message);
            handleSetInLineAlert({ type: 'error', heading: error.message });
          });
      }

      setLoading(false);
    },
    [
      handleChangeStep,
      onError,
      onSuccess,
      handleSetInLineAlert,
      order,
      isGuestCreateReturn,
    ]
  );

  return {
    order,
    steps,
    loading,
    formsRef,
    attributesList,
    selectedProductList,
    itemsEligibleForReturn,
    handleSelectedProductList,
    handleSetQuantity,
    handleChangeStep,
    onSubmit,
  };
};
