import { FunctionComponent } from 'preact';
import { Button, Checkbox } from '@adobe-commerce/elsie/components';
import { ReturnOrderProductListProps } from '@/order/types/createReturn.types';
import { CartSummaryItem } from '@/order/components';
import { OrderItemModel } from '@/order/data/models';
import { classes, Slot } from '@adobe-commerce/elsie/lib';
import '@/order/components/ReturnOrderProductList/ReturnOrderProductList.css';

export const ReturnOrderProductList: FunctionComponent<
  ReturnOrderProductListProps
> = ({
  placeholderImage,
  itemsEligibleForReturn,
  slots,
  loading,
  taxConfig,
  translations,
  selectedProductList,
  handleSelectedProductList,
  showConfigurableOptions,
  handleSetQuantity,
  handleChangeStep,
}) => {
  return (
    <ul className={'order-return-order-product-list'}>
      {itemsEligibleForReturn?.map((item, index) => {
        const { quantityReturnRequested, quantityShipped, eligibleForReturn } =
          item;

        const checked = selectedProductList.some(
          (el: OrderItemModel) =>
            el?.productSku === item.productSku && item.eligibleForReturn
        );
        const disabled =
          (quantityShipped === quantityReturnRequested && eligibleForReturn) ||
          !eligibleForReturn;

        const totalQuantity =
          quantityShipped - quantityReturnRequested === 0
            ? quantityReturnRequested
            : quantityShipped - quantityReturnRequested;

        return (
          <li
            key={item.id}
            className={classes([
              'order-return-order-product-list__item',
              ['order-return-order-product-list__item--blur', disabled],
            ])}
          >
            <Checkbox
              data-testid={`key_${index}`}
              name={`key_${index}`}
              checked={checked}
              disabled={disabled}
              onChange={() => {
                handleSelectedProductList({
                  ...item,
                  currentReturnOrderQuantity: 1,
                });
              }}
            />
            <CartSummaryItem
              slots={slots}
              placeholderImage={placeholderImage}
              loading={loading}
              product={{ ...item, totalQuantity: totalQuantity || 1 }}
              itemType={''}
              taxConfig={taxConfig}
              translations={translations}
              showConfigurableOptions={showConfigurableOptions}
              disabledIncrementer={!checked}
              onQuantity={
                totalQuantity > 1
                  ? (value) => {
                      handleSetQuantity(value, item.productSku!);
                    }
                  : undefined
              }
            />
            <Slot
              data-testid="returnOrderItem"
              name="ReturnOrderItem"
              slot={slots?.ReturnOrderItem}
            />
          </li>
        );
      })}
      <li className="order-return-order-product-list__item">
        <Button
          type="button"
          onClick={() => handleChangeStep('attributes')}
          disabled={!selectedProductList.length}
        >
          {translations.nextStep}
        </Button>
      </li>
    </ul>
  );
};
