import { Container, classes } from '@adobe-commerce/elsie/lib';
import { CreateReturnProps } from '@/order/types/createReturn.types';
import {
  OrderProductListSkeleton,
  ReturnOrderMessage,
  ReturnOrderProductList,
  ReturnReasonForm,
} from '@/order/components';
import { StepsTypes } from '@/order/types';
import {
  useCreateReturn,
  useInLineAlert,
  useGetStoreConfig,
} from '@/order/hooks';
import { useText } from '@adobe-commerce/elsie/i18n';
import { Header, InLineAlert } from '@adobe-commerce/elsie/components';
import { h } from 'preact';
import { setTaxStatus } from '@/order/lib/setTaxStatus';
import { OrderDataModel } from '@/order/data/models';

export const CreateReturn: Container<CreateReturnProps> = ({
  className,
  orderData,
  slots,
  onSuccess,
  onError,
  routeReturnSuccess,
  showConfigurableOptions,
}) => {
  const translations = useText({
    headerText: 'Order.CreateReturn.headerText',
    successTitle: 'Order.CreateReturn.success.title',
    successMessage: 'Order.CreateReturn.success.message',
    sender: 'Order.CreateReturn.giftCard.sender',
    recipient: 'Order.CreateReturn.giftCard.recipient',
    message: 'Order.CreateReturn.giftCard.message',
    outOfStock: 'Order.CreateReturn.stockStatus.outOfStock',
    nextStep: 'Order.CreateReturn.buttons.nextStep',
    backStep: 'Order.CreateReturn.buttons.backStep',
    submit: 'Order.CreateReturn.buttons.submit',
    backStore: 'Order.CreateReturn.buttons.backStore',
    downloadableCount: 'Order.CreateReturn.downloadableCount',
    returnedItems: 'Order.CreateReturn.returnedItems',
    configurationsListQuantity:
      'Order.CreateReturn.configurationsList.quantity',
  });

  const { inLineAlertProps, handleSetInLineAlert } = useInLineAlert();
  const storeConfig = useGetStoreConfig();

  const {
    order,
    itemsEligibleForReturn,
    formsRef,
    attributesList,
    steps,
    loading,
    selectedProductList,
    handleSelectedProductList,
    handleSetQuantity,
    handleChangeStep,
    onSubmit,
  } = useCreateReturn({
    orderData,
    onSuccess,
    onError,
    handleSetInLineAlert,
  });

  if (loading)
    return (
      <div>
        <OrderProductListSkeleton />
      </div>
    );

  if (!loading && !attributesList.length) return <div />;

  const placeholderImage = storeConfig?.baseMediaUrl ?? '';

  const renderList: Record<StepsTypes, h.JSX.Element | null> = {
    products: (
      <ReturnOrderProductList
        itemsEligibleForReturn={itemsEligibleForReturn}
        placeholderImage={placeholderImage}
        taxConfig={setTaxStatus(storeConfig?.shoppingOrderDisplayPrice ?? 0)}
        slots={slots}
        translations={translations}
        loading={loading}
        selectedProductList={selectedProductList}
        handleSelectedProductList={handleSelectedProductList}
        showConfigurableOptions={showConfigurableOptions}
        handleSetQuantity={handleSetQuantity}
        handleChangeStep={handleChangeStep}
      />
    ),
    attributes: (
      <ReturnReasonForm
        placeholderImage={placeholderImage}
        slots={slots}
        formsRef={formsRef}
        loading={loading}
        fieldsConfig={attributesList}
        selectedProductList={selectedProductList}
        handleChangeStep={handleChangeStep}
        translations={translations}
        onSubmit={onSubmit}
      />
    ),
    success: (
      <ReturnOrderMessage
        translations={translations}
        routeReturnSuccess={routeReturnSuccess}
        orderData={order as OrderDataModel}
      />
    ),
    error: null,
  };

  return (
    <div className={classes(['order-create-return', className])}>
      <Header title={translations.headerText} />
      {inLineAlertProps.heading ? (
        <InLineAlert
          className="order-create-return_notification"
          variant="secondary"
          data-testid="orderCreateReturnNotification"
          {...inLineAlertProps}
        />
      ) : null}
      {renderList[steps]}
    </div>
  );
};
