import { useState, useCallback } from 'preact/hooks';
import { InLineAlertProps } from '../types';
import {
  CheckWithCircle as Success,
  Warning,
  WarningWithCircle as Error,
} from '@adobe-commerce/elsie/icons';
import { Icon } from '@adobe-commerce/elsie/components';

const iconsList = {
  success: Success,
  warning: Warning,
  error: Error,
};

export const useInLineAlert = () => {
  const [inLineAlertProps, setInLineAlertProps] = useState<InLineAlertProps>({
    type: 'success',
    heading: '',
  });

  const handleSetInLineAlert = useCallback(
    (notification: InLineAlertProps | undefined) => {
      if (!notification?.type) {
        setInLineAlertProps({ type: 'success', heading: '' });
        return;
      }

      const icon = <Icon source={iconsList[notification.type]} />;

      setInLineAlertProps({
        ...notification,
        icon,
      });
    },
    []
  );

  return { inLineAlertProps, handleSetInLineAlert };
};
